import React, { Component } from "react";

class ErrorBoundary extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active: false,
    };
  }
  isActive = () => {
    return this.state.active ? " Active" : " Not Active";
  };
  onClickHandler = () => {
    this.setState({ active: true });
  };
  componentDidMount() {
    console.log('created')
  }
  componentDidUpdate() {
    console.log('updated')
  }
  componentWillUnmount(){
    console.log('unmounted')
  }
  render() {
    return (
      <div>
        <button onClick={this.onClickHandler}>Activate</button>
        <h1>
          Error Boundary{this.props.saludo}
          {this.isActive()}
        </h1>
      </div>
    );
  }
}

export default ErrorBoundary;
